.vertical-toggle-panel {
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  width: 2rem; // 32px on mobile
  min-width: 2rem;
  max-width: 2rem;
  overflow: visible;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #f3f4f6;
  transition: all 0.3s;
  z-index: 10;

  @media (min-width: 640px) {
    width: 2.5rem; // 40px on larger screens
    min-width: 2.5rem;
    max-width: 2.5rem;
  }
}

.vertical-toggle-panel button {
  outline: none;
  box-shadow: 0 0 0 2px var(--navigation-font-color);
}

.vertical-toggle-button {
  writing-mode: vertical-lr;
  transform: rotate(180deg);
  letter-spacing: 0.1em;
}
